import { NgModule, NO_ERRORS_SCHEMA } from '@angular/core';
import { CommonModule } from '@angular/common';

import { SharedModule } from '../../../shared/modules/shared.module';

import { ProductGroupMasterRoutingModule } from './product-group-master-routing.module';
import { ProductGroupMasterComponent } from './product-group-master.component';

@NgModule({
  imports: [
    CommonModule,
    SharedModule,
    ProductGroupMasterRoutingModule,
  ],
  declarations: [
    ProductGroupMasterComponent,
  ],
  schemas: [ NO_ERRORS_SCHEMA ]
})
export class ProductGroupMasterModule { }



